import "./styles.scss";
import {SERVER_LINK, APP_SUPPORTED} from '../../global/Variables';
import {findWithAttr} from '../../global/Functions';
import {Link, Redirect} from 'react-router-dom';
import {useState} from 'react';
import {FaCheck, FaBookmark, FaThumbsUp, FaThumbsDown} from 'react-icons/fa';
import ImdbIcon from '../../assets/imdb-logo.png';
import {WatchlistActions} from '../../views/Movie/controller';

export const MovieCard = (props) => {
    const [redirect, setRedirect] = useState(false);

    const openDetail = () => {
        localStorage.setItem('detailRedirectData', JSON.stringify(props.data));
        setRedirect(true);
    }

    if(redirect) return <Redirect to={`movie${props.data.movie_id || props.data._id ? '/' + (props.data.movie_id || props.data._id) : ''}`}/>

    return(
        <div className="card movie" style={{backgroundImage: `url(${props.data.poster})`}} onClick={openDetail}>

        </div>
    )
}

export const AppCard = (props) => {
    const apps = APP_SUPPORTED;
    if(props.type === 'add') {
        return(
            <div className="card app add" onClick={props.onClick}>
                +
            </div>
        )
    }

    if(apps[findWithAttr(apps, 'value', props.icon)]) {
        return(
            <a className="card app" href={`${props.link ? props.link : '#'}`} target="_blank">
                <div className="icon" style={props.style ? {...props.style, ...{background: `url(${apps[findWithAttr(apps, 'value', props.icon)].icon}) no-repeat center center / cover`}} : {background: `url(${apps[findWithAttr(apps, 'value', props.icon)].icon}) no-repeat center center / cover`}}></div>
                {props.appName ? <span className="name">{apps[findWithAttr(apps, 'value', props.icon)].name}</span> : null}
            </a>
        )
    }

    return (
        <div></div>
    )
}

export const MovieTitleCard = (props) => {
    const data = props.data;
    const {watchlist, like, dislike, seen,loading, watchlistSubmit, seenSubmit, likeSubmit, dislikeSubmit} = WatchlistActions(data._id)

    return( 
        <div className="card movie_title">
            <div className="poster" style={{backgroundImage: `url(${data.poster})`}}>
                <div className="actions_group">
                    <div className="group">
                        <div className={`action_btn ${watchlist ? 'active' : ''}`}>
                            <div className="container" onClick={() => {watchlistSubmit(); props.onActionSubmited()}}>
                                <FaBookmark className="icon"/>
                            </div>
                        </div>
                        <div className={`action_btn ${seen ? 'active' : ''}`}>
                            <div className="container" onClick={() => {seenSubmit(); props.onActionSubmited()}}>
                                <FaCheck className="icon"/>
                            </div>
                        </div>
                        <div className={`action_btn ${like ? 'active' : ''}`}>
                            <div className="container" onClick={() => {likeSubmit(); props.onActionSubmited()}}>
                                <FaThumbsUp className="icon"/>
                            </div>
                        </div>
                        <div className={`action_btn ${dislike ? 'active' : ''}`}>
                            <div className="container" onClick={() => {dislikeSubmit(); props.onActionSubmited()}}>
                                <FaThumbsDown className="icon"/>
                            </div>
                        </div>
                    </div>
                    <div className="rating">
                        <img src={ImdbIcon} className="icon"/>
                        <span>{data.rating}</span>
                    </div>
                </div>
            </div>
            <div className="information_container">
                <div className="header">
                    <Link to={`/movie/${data._id}`} className="title">{data.title} <span className="year">({data.release_year})</span></Link>
                    <div className={`mark_btn ${seen ? 'active' : ''}`} onClick={() => {seenSubmit(); props.onActionSubmited()}}>
                        <FaCheck className="icon"/>
                    </div>
                </div>
                <p className="overview">{data.overview}</p>
                <div className="rating">
                    <img src={ImdbIcon} className="icon"/>
                    <span>{data.rating}</span>
                </div>
                <Link to={`/movie/${data._id}`} className="watch_btn">
                    <AppCard icon={data.apps[0].app} style={{width: 20, height: 20, marginRight: 4}}/>
                    <span>Watch now</span>
                </Link>
            </div>
        </div>
    )
}

export const MovieRowCard = (props) => {
    const data = props.data;
    const apps = props.data.apps;
    const rentApps = [];
    const freeApps = [];

    if(apps) {
        //FILTER RENT APPS
        apps.forEach((item ,i) => {
            if(item.rent) rentApps.push(item);
        })
    
        //FILTER FREE APPS
        apps.forEach((item ,i) => {
            if(!item.rent) freeApps.push(item);
        })
    }


    return(
        <div className="card movie_row">
            <Link className="poster" style={{backgroundImage: `url(${data.poster})`}} to={`/movie/${data._id}`}></Link>
            <div className="information_container">
                <Link className="title" to={`/movie/${data._id}`}>{data.title} {data.release_year ? <span className="year">({data.release_year})</span> : ''}</Link>
                {rentApps.length && freeApps.length ? <span className="subtitle">WATCHNOW</span> : ''}
                <div className="apps_supported_group">
                    {freeApps.length ? 
                    <div className="col">
                        <div className="header"><span>FREE</span></div>
                        <div className="list">
                            {freeApps.map((data, i) => {
                                return <AppCard icon={data.app} appName style={{width: 50, height: 50}} link={data.link}/>
                            })}
                        </div>
                    </div> : null}
                    {rentApps.length ? 
                    <div className="col">
                        <div className="header second"><span>RENT</span></div>
                        <div className="list">
                            {rentApps.map((data, i) => {
                                return <AppCard icon={data.app} appName style={{width: 50, height: 50}} link={data.link}/>
                            })}
                        </div>
                    </div>: null}
                    {freeApps.length ? 
                    <div className="col">
                        <div className="header third"><span>WEBSITES</span></div>
                        <div className="list">
                            {freeApps.map((data, i) => {
                                return <AppCard icon={data.app} appName style={{width: 50, height: 50}} link={data.link}/>
                            })}
                        </div>
                    </div>: null}
                </div>
                {data.rating ? 
                    <div className="rating">
                        <img src={ImdbIcon} className="icon"/>
                        <span>{data.rating}</span>
                    </div> : null}
            </div>
        </div>
    )
}