import {useState, useEffect} from 'react';
import {Selectbox} from '../../components/Selectbox';
import {LoadingScreen} from '../../components/Loading';
import {API} from '../../global/Functions';
import {MoviesRowList} from '../../components/List';
import {FaFilter} from 'react-icons/fa';
import "./styles.scss";

const Search = (props) => {
    const [selectboxVisible, setSelectboxVisilble] = useState(false);
    const [movies, setMovies] = useState(null);
    const searchValue = props.match.params.value;

    useEffect(() => {
        API.post(`/search/${searchValue}`, {}, res => {
            setMovies(res.data);
        })
    }, [])

    if(!movies) {
        return <LoadingScreen/>
    }

    return(
        <div className="view">
            <div className="header">
                <h1>Search results for: Unknown</h1>
                <div className="action_btn">
                    <span>Filters</span>
                    <FaFilter className="icon"/>
                </div>
            </div>
            <div className="box" onClick={() => setSelectboxVisilble(true)}>
                <span>128 titles</span>
                <Selectbox visible={selectboxVisible}/>
            </div>
            <MoviesRowList data={movies}/>
        </div>
    )
}

export default Search;