import "./styles.scss";

import {MovieCard, AppCard, MovieTitleCard, MovieRowCard} from "../Card";

export const DiscoveryList = (props) => {
    return(
        <div className="list discovery">
            {props.title ? <span className="list title">{props.title}</span> : null}
            <div className="container">
                {props.data.map((data, i) => {
                    return <MovieCard data={data} key={`movie-${i}`}/>
                })}
            </div>
        </div>
    )
}

export const AppsFilterList = (props) => {
    return(
        <div className="list apps_filter">
            <div className="container">
                {props.appsSelected.map((data, i) => {
                    return <AppCard icon={data} onClick={props.onOpenModal} key={`app-filter-list-${i}`}/>
                })}
                <AppCard type="add" onClick={props.onOpenModal}/>
            </div>
        </div>
    )
}

export const TitleGridList = (props) => {
    return(
        <div className="list title_grid">
            {props.data.map((data, i) => {
                return <MovieTitleCard data={data} key={`movie-title-card-${i}`} onActionSubmited={props.onActionSubmited}/>
            })}
        </div>
    )
}

export const MoviesRowList = (props) => {
    return(
        <div className="list row">
            {props.data.map((data, i) => {
                return <MovieRowCard data={data} key={`movie-row-card-${i}`}/>
            })}
        </div>
    )
}

export const MoviesBoxList = (props) => {
    return(
        <div className="list box">
            {props.data.map((data, i) => {
                return <MovieCard data={data} key={`movie-box-${i}`}/>
            })}
        </div>
    )
}