import {FaList, FaBars, FaAngleDown, FaFilter, FaTh, FaThumbsUp, FaThumbsDown, FaBookmark} from 'react-icons/fa';
import {useState} from 'react';
import "./styles.scss";

export const Selectbox = (props) => {
    const [value, setValue] = useState('newest');
    const visible = props.visible ? 'show' :'';

    const select = (value) => {
        setValue(value);
        props.onChange(value);
    }
    
    return(
        <div className="selectbox">
            <span className="label" onClick={props.onOpen}>sorted by {value.charAt(0).toUpperCase() + value.slice(1)} <FaAngleDown className="icon"/></span>
            <div className={`backdrop ${visible}`} onClick={props.onClose}></div>
            <div className={`popup ${visible}`}>
                <div className={`item ${value === 'newest' ? 'active' : ''}`} onClick={() => select('newest')}>Newest</div>
                <div className={`item ${value === 'oldest' ? 'active' : ''}`} onClick={() => select('oldest')}>Oldest</div>
                <div className={`item ${value === 'random' ? 'active' : ''}`} onClick={() => select('random')}>Random</div>
                <div className={`item ${value === 'alphabetical' ? 'active' : ''}`} onClick={() => select('alphabetical')}>Alphabetical</div>
                <div className={`item ${value === 'release year' ? 'active' : ''}`} onClick={() => select('release year')}>Release year</div>
                <div className={`item ${value === 'rating' ? 'active' : ''}`} onClick={() => select('rating')}>Rating</div>
            </div>
        </div>
    )
}