import {IoCheckmarkSharp} from 'react-icons/io5';
import {FaList, FaBars, FaAngleDown, FaFilter, FaTh, FaThumbsUp, FaThumbsDown, FaBookmark} from 'react-icons/fa';
import {TitleGridList, MoviesRowList, MoviesBoxList} from '../../components/List';
import {API, findWithAttr, arrayShuffle} from '../../global/Functions';
import {useState, useEffect} from 'react';
import {LoadingScreen} from '../../components/Loading';
import {MoviesFilterModal} from '../../components/Modal';
import {Selectbox} from '../../components/Selectbox';
import "./styles.scss";

const Watchlist = (props) => {
    const [watchlist, setWatchlist] = useState(null);
    const [defaultWatchlist, setDefaultWatchlist] = useState(null);
    const [seenList, setSeenList] = useState(null);
    const [defaultSeenList, setDefaultSeenList] = useState(null);
    const [likeList, setLikeList] = useState(null);
    const [defaultLikeList, setDefaultLikeList] = useState(null);
    const [dislikeList, setDislikeList] = useState(null);
    const [defaultDislikeList, setDefaultDislikeList] = useState(null);
    const [tab, setTab] = useState(0);
    const [listOption, setListOption] = useState(0);
    const [filterModalVisible, setFilterModalVisible] = useState(false);
    const [selectboxVisible, setSelectboxVisible] = useState(false);
    
    useEffect(() => {
        fetchData();
    }, [])

    const fetchData = () => {
        const watchlist = localStorage.getItem('@watchlist')
        const seenlist = localStorage.getItem('@seenlist')
        const likelist = localStorage.getItem('@likelist')
        const dislikelist = localStorage.getItem('@dislikelist')

        let list = {
            watchlist: watchlist || [],
            seenlist: seenlist || [],
            likelist: likelist || [],
            dislikelist: dislikelist || []
        }

        API.get('/get_movies', res => {
            let movies = res;
            //FILTER WATCHLIST BY MOVIE ID
            let watchlistData = movies.filter((data, i) => {
                return list.watchlist.indexOf(data._id) > -1;
            })
            let seenListData = movies.filter((data, i) => {
                return list.seenlist.indexOf(data._id) > -1;
            })
            let likeListData = movies.filter((data, i) => {
                return list.likelist.indexOf(data._id) > -1;
            })
            let dislikeListData = movies.filter((data, i) => {
                return list.dislikelist.indexOf(data._id) > -1; 
            });

            setWatchlist(watchlistData.reverse());
            setSeenList(seenListData.reverse());
            setLikeList(likeListData.reverse());
            setDislikeList(dislikeListData.reverse());

            //SET DEFAULT DATA
            setDefaultWatchlist(watchlistData.reverse());
            setDefaultSeenList(seenListData.reverse());
            setDefaultLikeList(likeListData.reverse());
            setDefaultDislikeList(dislikeListData.reverse());
        })
    }

    //FILTER MOVIE
    const Filter = (options) => {
        console.log(options);
        let moviesFiltered = tab === 0 ? defaultWatchlist : tab === 1 ? defaultSeenList : tab === 2 ? defaultLikeList : defaultDislikeList; //DEFAULT TAB MOVIE DATA

        //FILTER BY APPS AND TYPES
        moviesFiltered =  moviesFiltered.filter((data, i) => {
            let appsFiltered = data.apps.filter(app => {
                //User select apps and types
                if(options.apps.length) { 
                    if(options.types.indexOf('rent') > -1 && app.rent && options.apps.indexOf(app.app) > -1) return true; //User select rent, app are rent and app was selected in filter options then return true
                    if(options.types.indexOf('free') > -1 && !app.rent && options.apps.indexOf(app.app) > -1) return true; //User select free,...
                    if(!options.types.length && options.apps.indexOf(app.app) > -1) return true; //User not select types;
                }else{//Just select types
                    if(options.types.length) {
                        if(options.types.indexOf('rent') > -1 && app.rent) return true;
                        if(options.types.indexOf('free') > -1 && !app.rent) return true;
                    }else{
                        return true;
                    }
                }

                return false;
            })
            return appsFiltered.length;
        })

        //FILTER BY RELEASE YEAR
        if(options.releaseYear[0] == 1900 && options.releaseYear[1] == new Date().getFullYear()) { //IF IS NOT DEFAULT OPTION
            
        }else{
            moviesFiltered = moviesFiltered.filter(data => {
                return (options.releaseYear[0] <= parseInt(data.release_year) && parseInt(data.release_year) <= options.releaseYear[1]); //Return start <= this year <= end year
            })
        }

        //FILTER BY RATING
        if(options.rating[0] === 0 && options.rating[1] === 10) {//IF IS NOT DEFAULT OPTION

        }else{
            moviesFiltered = moviesFiltered.filter(data => {
                let Rating = data.rating ? data.rating.substring(0, 3) : 0; //Get 3 character at start;
                
                return (options.rating[0] <= parseFloat(Rating) && parseFloat(Rating) <= options.rating[1]);
            })
        }

        //FILTER BY GENRES
        if(options.genres.length) {
            moviesFiltered = moviesFiltered.filter(data => {
                let genresFiltered = options.genres.filter(genres => {
                    let movieGenres = data.genres ? data.genres.replace(/,/g, ' ').toLowerCase() : '';
    
                    return data.genres ? movieGenres.indexOf(genres) > -1 : false;
                })
    
                return genresFiltered.length;
            })
        }

        switch(tab) {
            case 0 : setWatchlist(moviesFiltered);
            case 1 : setSeenList(moviesFiltered);
            case 2 : setLikeList(moviesFiltered);
            case 3 : setDislikeList(moviesFiltered);

            break;
        }
    }

    //SORT MOVIE
    const Sort = (value) => {
        let sortData = tab === 0 ? [...defaultWatchlist] : tab === 1 ? [...defaultSeenList] : tab === 2 ? [...defaultLikeList] : [...defaultDislikeList];
        
        switch(value) {
            case 'oldest' : {
                sortData = sortData.reverse();
                break;
            }
            case 'random' : {
                sortData = arrayShuffle(sortData);
                break;
            }
            case 'alphabetical': {
                sortData = sortData.sort((a, b) => {
                    if(a.title < b.title) { return -1; }
                    if(a.title > b.title) { return 1; }
                    return 0;
                })
            }
            case 'release year': {
                sortData = sortData.sort((a, b) => {
                    let aRelease = parseInt(a.release_year);
                    let bRelease = parseInt(b.release_year);

                    if(aRelease < bRelease) { return -1; }
                    if(aRelease > bRelease) { return 1; }
                    return 0;
                })
            }
            case 'rating': {
                sortData = sortData.sort((a, b) => {
                    let aRating = parseFloat(a.rating);
                    let bRating = parseFloat(b.rating);

                    if(aRating < bRating) { return -1; }
                    if(aRating > bRating) { return 1; }
                    return 0;
                })
            }
        }

        if(tab === 0) {
            setWatchlist(sortData);
        }else if(tab === 1) {
            setSeenList(sortData);
        }else if(tab === 2) {
            setLikeList(sortData);
        }else{
            setDislikeList(sortData);
        }
        
        //Hide popup
        setSelectboxVisible(false);
    }

    if(!watchlist || !likeList || !dislikeList || !seenList) return <LoadingScreen/>

    return(
        <div className="view">
            <h4 className="custom_header">Movies</h4>
            <MoviesFilterModal visible={filterModalVisible} onClose={() => setFilterModalVisible(false)} onFilter={Filter}/>

            <div className="tab_controller_container">
                <div className={`tab ${tab === 0 ? 'active' : ''}`} onClick={() => setTab(0)}>
                    <FaBookmark className="icon"/>
                    <span>Watchlist</span>
                </div>
                <div className={`tab ${tab === 1 ? 'active' : ''}`} onClick={() => setTab(1)}>
                    <IoCheckmarkSharp  className="icon"/>
                    <span>Seen</span>
                </div>
                <div className={`tab ${tab === 2 ? 'active' : ''}`} onClick={() => setTab(2)}>
                    <FaThumbsUp className="icon"/>
                    <span>Like</span>
                </div>
                <div className={`tab ${tab === 3 ? 'active' : ''}`} onClick={() => setTab(3)}>
                    <FaThumbsDown className="icon"/>
                    <span>Dislike</span>
                </div>
            </div>

            <div className="options_container">
                <div className="options-left">
                    <span className="option">{tab === 0 && watchlist.length || tab === 1 && seenList.length || tab === 2 && likeList.length || tab === 3 && dislikeList.length ? tab === 0 ? watchlist.length : tab === 1 ? seenList.length : tab === 2 ? likeList.length : dislikeList.length : 0} titles</span>
                    <Selectbox visible={selectboxVisible} onChange={Sort} onOpen={() => setSelectboxVisible(true)} onClose={() => setSelectboxVisible(false)}/>
                </div>
                <div className="options-right">
                    <div className="option_box">
                        <div className={`option_btn ${listOption === 0 ? 'active' : ''}`} onClick={() => setListOption(0)}>
                            <FaList className="icon"/>
                        </div>
                        <div className={`option_btn ${listOption === 1 ? 'active' : ''}`} onClick={() => setListOption(1)}>
                            <FaBars className="icon"/>
                        </div>
                        <div className={`option_btn ${listOption === 2 ? 'active' : ''}`} onClick={() => setListOption(2)}>
                            <FaTh className="icon"/>
                        </div>
                    </div>
                    <div className="option_box">
                        <div className="option_chip_btn" onClick={() => setFilterModalVisible(true)}>
                            <FaFilter className="icon"/>
                            <span>Filters</span>
                        </div>
                    </div>
                </div>
            </div>

            {listOption === 0 ? 
                <TitleGridList data={tab === 0 ? watchlist : tab === 1 ? seenList : tab === 2 ? likeList : dislikeList} onActionSubmited={fetchData}/> :
            listOption === 1 ? <MoviesRowList data={tab === 0 ? watchlist : tab === 1 ? seenList : tab === 2 ? likeList : dislikeList}/> :
                <MoviesBoxList data={tab === 0 ? watchlist : tab === 1 ? seenList : tab === 2 ? likeList : dislikeList}/>
            }
            {tab === 0 && !watchlist.length || tab === 1 && !seenList.length || tab === 2 && !likeList.length || tab === 3 && !dislikeList.length ? <span className="message">You haven't {tab === 0 ? 'bookmark' : tab === 1 ? 'seen' : tab === 2 ? 'liked' : 'disliked'} any movies yet.</span> : <span></span>}
        </div>
    )
}

export default Watchlist;