import {Link} from 'react-router-dom';
import {IoSearch} from 'react-icons/io5';
import {useState, useRef, useEffect} from 'react';
import {LoadingSpiner} from '../../components/Loading';
import NoActorImage from '../../assets/no-actor.png';
import {API, arrayMove} from '../../global/Functions';
import "./styles.scss";

export const NavigationBar = (props) => {
    const [path, setPath] = useState(window.location.pathname);
    const [searchSuggestBoxVisible, setSearchSuggestBoxVisible] = useState(false);
    const [typingTimeout, setTypingTimeout] = useState(0);
    const [searchData, setSearchData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [searchRecent, setSearchRecent] = useState(JSON.parse(localStorage.getItem('@recentSearch')) || []);
    const [searchValue, setSearchValue] = useState('');
    
    //SEARCH FUNCTION
    const search = (value) => {
        if(value !== '') {
            if (typingTimeout) {
                clearTimeout(typingTimeout);
            }
            setLoading(true);
            setSearchValue(value);
            //WAIT USER TYPING DONE
            setTypingTimeout(setTimeout(() => {
                console.log("A");
                API.post(`/search/${value}`, {}, res => {
                    setSearchData(res.data);
                    setLoading(false);
                })
            }, 1000))
        }else{
            setLoading(false);
            setSearchValue(value);
        }
    }

    //OPEN MOVIE DETAIL
    const openDetail = (data) => {
        //SET SEARCH DATA TO LOCALSTORAGE
        localStorage.setItem('detailRedirectData', JSON.stringify(data));

        //GET RECENT
        let recentData = searchRecent;

        //SEARCH VALUE IS EXIST IN RECENT DATA
        if(recentData.indexOf(searchValue) > -1) {
            //REMOVE IT IN ARRAY
            recentData = arrayMove(recentData, recentData.indexOf(searchValue), 0);
        }else{
            //PUSH IT TO START
            recentData.push(searchValue);
            recentData = recentData.reverse();
        }

        //SAVE TO DATA
        localStorage.setItem('@recentSearch', JSON.stringify(recentData));
        setSearchRecent(recentData);
        
        //REDIRECT TO MOVIE
        window.location = '/movie';
    }

    //CLEAR RECENT SEARCH
    const clearRecentSearch = () => {
        localStorage.removeItem("@recentSearch");
        setSearchRecent([]);
    }

    return(
        <nav className="navigation_bar">
            <div className="container">
                <div className="logo_container">
                    <h3>Logo</h3>
                </div>
                <ul className="menu_container">
                    <li className={path === '/' ? 'active' : ''} onClick={() => setPath('/')}><Link to="/">Home</Link></li>
                    <li className={path === '/new' ? 'active' : ''} onClick={() => setPath('/new')}><Link to="#">New</Link></li>
                    <li className={path === '/popular' ? 'active' : ''} onClick={() => setPath('/popular')}><Link to="#">Popular</Link></li>
                    <li className={path === '/watchlist' ? 'active' : ''} onClick={() => setPath('/watchlist')}><Link to="/watchlist">Watchlist</Link></li>
                </ul>
                <div className="search_container">
                    <IoSearch className="icon"/>
                    <input type="text" className="search_input" placeholder="Search for movies or tv shows" value={searchValue} onClick={() => setSearchSuggestBoxVisible(true)} onChange={(e) => search(e.target.value)}/>
                    <SearchSuggestionBox onRecentSearchClick={search} onClearRecent={clearRecentSearch} searchRecent={searchRecent} searchData={searchData} visible={searchSuggestBoxVisible} loading={loading} onOpenDetail={openDetail}/>
                </div>
                <div className={`search_backdrop ${searchSuggestBoxVisible ? 'show' : ''}`} onClick={() => setSearchSuggestBoxVisible(false)}></div>
                <div className="actions_container"></div>
            </div>
        </nav>
    )
}

const SearchSuggestionBox = (props) => {
    const [recentVisible, setRecentVisible] = useState(true);

    return(
        <div className={`search_suggestion ${props.visible ? 'show' : ''}`}>
            {recentVisible ? 
            <div className="recent_container">
                <div className="header">
                    <span className="title">Recent searches</span>
                    <span className="action_btn" onClick={props.onClearRecent}>Clear all</span>
                </div>
                <div className="list">
                    {props.searchRecent.map((data, i) => {
                        return(
                            <div className="item" key={`search-recent-item-${i}`} onClick={() => props.onRecentSearchClick(data)}>
                                <IoSearch className="icon"/>
                                <span>{data}</span>
                            </div>
                        )
                    })}
                </div>
            </div> : null}

            <div className="suggest_container">
                <div className="result_container">
                    <div className="col">
                        <div className="header">
                            <span className="title">MOVIES</span>
                        </div>
                        {props.loading ? 
                            <div className="loading_container">
                                <LoadingSpiner/>
                            </div> :
                            <div className="list">
                                {props.searchData.splice(0, 3).map((data, i) => {
                                    return(
                                        <div className="item" key={`search-item-${i}`} onClick={() => props.onOpenDetail(data)}>
                                            <div className="poster_container">
                                                <div className="poster" style={{backgroundImage: `url(${data.poster})`}}></div>
                                            </div>
                                            <div className="information_container">
                                                <span className="title">{data.title}</span>
                                                <span className="year">{data.release_year}</span>
                                            </div>
                                        </div>
                                    )
                                })}
                            </div>}
                    </div> 
                        
                    <div className="col">
                        <div className="header">
                            <span className="title">PEOPLE</span>
                        </div>
                        {props.loading ? 
                            <div className="loading_container">
                                <LoadingSpiner/>
                            </div> : 
                            <div className="list">
                                <div className="item">
                                    <div className="avatar_container">
                                        <div className="avatar" style={{backgroundImage: `url(${NoActorImage})`}}></div>
                                    </div>
                                    <div className="information_container">
                                        <span className="title">People</span>
                                    </div>
                                </div>
                            </div>}
                    </div>
                </div>
                <div className="foot">
                    <span>See all results for Unknown </span>
                </div>
            </div>
        </div>
    )
}