import {DiscoveryList, AppsFilterList} from "../../components/List";
import {AppsFilterModal} from '../../components/Modal';
import {useState, useEffect} from 'react';
import {API, arrayShuffle, findWithAttr} from '../../global/Functions';

const Home = (props) => {
    const [modalVisible, setModalVisible] = useState(false);
    const [movies, setMovies] = useState(null);
    const [oldMovies, setOldMovies] = useState(null);
    const [appsSelected, setAppsSelected] = useState([]);

    useEffect(() => {
        fetchData();
    }, [])

    const fetchData = async () => {
        let moviesData = []

        await API.get('/get_newst_movies/justwatch', async res => {
            let newResult = res;
            await newResult.forEach((data, i) => {
                newResult[i].type = 'justwatch';
            })

            moviesData = arrayShuffle(newResult)
        })

        await API.get('/get_newst_movies/danet', res => {
            moviesData.push({title: "Danet Hot", movies: res.result, type: "danet"})
        })

        await API.get('/get_newst_movies/fpt', res => {
            moviesData.push({title: "FPT Play Hot", movies: res.result, type: "fpt"})
        })

        await API.get('/get_newst_movies/zing', res => {
            moviesData.push({title: "Zing TV Hot", movies: res.result, type: "zing"})
        })

        await API.get('/get_newst_movies/cliptv', res => {
            moviesData.push({title: "Clip TV Hot", movies: res.result, type: "cliptv"})
        })

        await API.get('/get_newst_movies/galaxy', res => {
            moviesData.push({title: "Galaxy Play Hot", movies: res.result, type: "galaxy"})
        })

        await API.get('/get_newst_movies/pops', res => {
            moviesData.push({title: "POPS Hot", movies: res.result, type: "pops"})
        })

        await API.get('/get_newst_movies/vieon', res => {
            moviesData.push({title: "VieON Hot", movies: res.result, type: "vieon"})
        })

        await API.get('/get_newst_movies/iqiyi', res => {
            moviesData.push({title: "iQiyi Hot", movies: res.result, type: "iqiyi"})
        })

        await API.get('/get_newst_movies/dongphym', res => {
            moviesData.push({title: "Dong Phym Hot", movies: res.result, type: "dongphym"})
        })

        await API.get('/get_newst_movies/motphjm', res => {
            moviesData.push({title: "Mot Phjm Hot", movies: res.result, type: "motphjm"})
        })

        await API.get('/get_newst_movies/luotphim', res => {
            moviesData.push({title: "Luot Phim Hot", movies: res.result, type: "luotphim"})
        })

        moviesData = arrayShuffle(moviesData);

        setMovies(moviesData);
        setOldMovies(moviesData);
    }

    const filter = async (apps) => {
        if(apps.length) {
            let justWatchVisible = apps.indexOf('google play movies') > -1 || apps.indexOf('apple itunes') > -1 || apps.indexOf('netflix') > -1 || apps.indexOf('hbo now') > -1 || apps.indexOf('hbo max') > -1 || apps.indexOf('imdb tv') > -1 || apps.indexOf('amazon video') > -1 ? true : false;
            let filterMovies =  oldMovies.filter((data, i) => {
                if(justWatchVisible && data.type === 'justwatch') return true;
                return apps.indexOf(data.type) > -1;
            })

            setMovies(filterMovies);
            setAppsSelected(apps);
        }else{
            setMovies(oldMovies);
            setAppsSelected([]);
        }

        setModalVisible(false);
    }

    if(!movies) {
        return <div></div>
    }

    return(
        <div className="view">
            <h1>Movies and TV shows for you </h1>
            <AppsFilterList onOpenModal={() => setModalVisible(true)} appsSelected={appsSelected}/>
            <AppsFilterModal visible={modalVisible} onDone={filter}/>

            {movies.map((data, i) => {
                if(data.movies.length) {
                    return <DiscoveryList title={data.title} data={data.movies} key={`discovery-list-${i}`}/>
                }
            })}
        </div>
    )
}

export default Home;
