import {API} from '../../global/Functions';
import {useState, useEffect} from 'react';

export const WatchlistActions = (movieId) => {
    const [watchlist, setWatchlist] = useState(false);
    const [like, setLike] = useState(false);
    const [dislike, setDislike] = useState(false);
    const [seen, setSeen] = useState(false);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        getWatchlist();
    }, [])

    const getWatchlist = () => {
        let watchlist = localStorage.getItem('@watchlist');
        let seenlist = localStorage.getItem('@seenlist');
        let likelist = localStorage.getItem('@likelist');
        let dislikelist = localStorage.getItem('@dislikelist');

        watchlist = watchlist ? watchlist.split(",") : [];
        seenlist = seenlist ? seenlist.split(",") : [];
        likelist = likelist ? likelist.split(",") : [];
        dislikelist = dislikelist ? dislikelist.split(",") : [];

        setWatchlist(watchlist.indexOf(movieId) > -1)
        setSeen(seenlist.indexOf(movieId) > -1)
        setLike(likelist.indexOf(movieId) > -1)
        setDislike(dislikelist.indexOf(movieId) > -1)
        setLoading(false);
    }

    const watchlistSubmit = () => {
        let watchlist = localStorage.getItem('@watchlist');
        watchlist = watchlist ? watchlist.split(",") : [];

        //CHECK MOVIE IS EXIST
        if(watchlist.indexOf(movieId) > -1) {
            watchlist.splice(watchlist.indexOf(movieId), 1);
        }else{
            watchlist.push(movieId)
        }

        setWatchlist(watchlist.indexOf(movieId) > -1);

        watchlist = watchlist.join(",");
        localStorage.setItem("@watchlist", watchlist);
    }

    const seenSubmit = () => {
        let seenlist = localStorage.getItem("@seenlist");
        seenlist = seenlist ? seenlist.split(",") : [];

        //CHECK MOVIE IS EXIST
        if(seenlist.indexOf(movieId) > -1) {
            seenlist.splice(seenlist.indexOf(movieId), 1);
        }else{
            seenlist.push(movieId)
        }

        setSeen(seenlist.indexOf(movieId) > -1);

        seenlist = seenlist.join(",");
        localStorage.setItem("@seenlist", seenlist);
    }

    const likeSubmit = () => {
        let likelist = localStorage.getItem("@likelist");
        let dislikelist = localStorage.getItem('@dislikelist');
        likelist = likelist ? likelist.split(",") : [];
        dislikelist = dislikelist ? dislikelist.split(",") : [];

        //CHECK MOVIE IS EXIST
        if(likelist.indexOf(movieId) > -1) {
            likelist.splice(likelist.indexOf(movieId), 1);
        }else{
            likelist.push(movieId)
        }

        if(dislikelist.indexOf(movieId) > -1) {
            dislikelist.splice(dislikelist.indexOf(movieId), 1);
        }

        setLike(likelist.indexOf(movieId) > -1);
        setDislike(false);

        likelist = likelist.join(",");
        dislikelist = dislikelist.join(",");
        localStorage.setItem('@likelist', likelist);
        localStorage.setItem('@dislikelist', dislikelist);
    }

    const dislikeSubmit = () => {
        let likelist = localStorage.getItem('@likelist');
        let dislikelist = localStorage.getItem('@dislikelist');
        likelist = likelist ? likelist.split(",") : [];
        dislikelist = dislikelist ? dislikelist.split(",") : [];

        //CHECK MOVIE IS EXIST
        if(dislikelist.indexOf(movieId) > -1) {
            dislikelist.splice(dislikelist.indexOf(movieId), 1);
        }else{
            dislikelist.push(movieId)
        }

        if(likelist.indexOf(movieId) > -1) {
            likelist.splice(likelist.indexOf(movieId), 1);
        }

        setDislike(dislikelist.indexOf(movieId) > -1);
        setLike(false);

        likelist = likelist.join(",");
        dislikelist = dislikelist.join(",");
        localStorage.setItem('@likelist', likelist);
        localStorage.setItem('@dislikelist', dislikelist);
    }

    return {watchlist, like, dislike, seen,loading, getWatchlist, watchlistSubmit, seenSubmit, likeSubmit, dislikeSubmit};
}