import {IoCheckmarkSharp} from 'react-icons/io5';
import {FaFacebook, FaTwitter ,FaRegCopy, FaThumbsUp, FaThumbsDown, FaBookmark} from 'react-icons/fa';
import {AppCard} from '../../components/Card';
import {useState, useEffect} from 'react';
import {API, findWithAttr} from '../../global/Functions';
import {APP_SUPPORTED} from '../../global/Variables';
import {DiscoveryList} from '../../components/List';
import {LoadingScreen} from '../../components/Loading';
import {WatchlistActions} from './controller';
import "./styles.scss";

const ActionContainer = (props) => {
    const {watchlist, like, dislike, seen,loading, watchlistSubmit, seenSubmit, likeSubmit, dislikeSubmit} = WatchlistActions(props.movieId)

    return (
        <div className="action_container">
            <div className={`item ${watchlist ? 'active' : ''}`} onClick={watchlistSubmit}>
                <FaBookmark className="icon"/>
                <span>Watchlist</span>
            </div>
            <div className={`item ${seen ? 'active' : ''}`} onClick={seenSubmit}>
                <IoCheckmarkSharp className="icon"/>
                <span>Seen</span>
            </div>
            <div className={`item ${like ? 'active' : ''}`} onClick={likeSubmit}>
                <FaThumbsUp className="icon"/>
                <span>Like</span>
            </div>
            <div className={`item ${dislike ? 'active' : ''}`} onClick={dislikeSubmit}>
                <FaThumbsDown className="icon"/>
                <span>Dislike</span>
            </div>
        </div>
    )
}

const InformationGroup = (props) => {
    const data = props.data;

    return(
        <div className="information_group">
            <hr/>
            <div className="clearfix">
                <span className="title">RATINGS</span>
                <span className="value">{data.rating}</span>
            </div>
            <div className="clearfix">
                <span className="title">GENRES</span>
                <span className="value">{data.genres}</span>
            </div>
            <div className="clearfix">
                <span className="title">RUNTIME</span>
                <span className="value">{data.runtime}</span>
            </div>
            <div className="clearfix">
                <span className="title">DIRECTOR</span>
                <span className="value">{data.director}</span>
            </div>
            <hr/>
        </div>
    )
}

const AppsSupportedGrid = (props) => {
    const [tab, setTab] = useState(0);
    const apps = props.apps;
    const quality = ["sd", "hd", "fhd", "4k"];

    return(
        <div className="apps_supported_group">
            <h4 className="title">WATCH NOW</h4>
            <div className="tab_container">
                <div className="tab_header">
                    <div className={`item ${tab === 0 ? 'active' : ''}`} onClick={() => setTab(0)}>
                        <span>SD</span>
                    </div>
                    <div className={`item ${tab === 1 ? 'active' : ''}`} onClick={() => setTab(1)}>
                        <span>HD</span>
                    </div>
                    <div className={`item ${tab === 2 ? 'active' : ''}`} onClick={() => setTab(2)}>
                        <span>FHD</span>
                    </div>
                    <div className={`item ${tab === 3 ? 'active' : ''}`} onClick={() => setTab(3)}>
                        <span>4K</span>
                    </div>
                </div>
                <div className="tab">
                    <div className="container">
                        <div className="grid">
                            <span className="header">FREE</span>
                            <div className="list">
                                {apps.map((data, i) => {
                                    if(!data.rent && quality.indexOf(data.quality) >= tab) return <AppCard key={`app-supported-free-${i}`} link={data.link} icon={data.app} style={{width: 50, height: 50}} appName/>
                                })}
                            </div>
                        </div>
                        <div className="grid">
                            <span className="header second">RENT</span>
                            <div className="list">
                                {apps.map((data, i) => {
                                    if(data.rent && quality.indexOf(data.quality) >= tab) return <AppCard key={`app-supported-rent-${i}`} link={data.link} icon={data.app} style={{width: 50, height: 50}} appName/>
                                })}
                            </div>
                        </div>
                        <div className="grid">
                            <span className="header third">WEBSITES</span>
                            <div className="list">
                                {apps.map((data, i) => {
                                    if(!data.rent && quality.indexOf(data.quality) >= tab) return <AppCard key={`app-supported-web-${i}`} link={data.link} icon={data.app} style={{width: 50, height: 50}} appName/>
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

const CastList = (props) => {
    return(
        <div className="castlist">
            {props.cast.split(",").map((data, i) => {
                return <span className="actor" key={`cast-${i}`}>{data}</span>
            })}
        </div>
    )
}

const Movie = (props) => {
    const [data, setData] = useState(null);
    const [appList, setAppList] = useState('');
    const [movieId, setMovieId] = useState(null);
    const [recommendMovies, setRecommendMovies] = useState(null);

    useEffect(() => {
        fetchData();
    },[])

    const fetchData = () => {
        if(props.match.params.id) {
            API.get(`/get_movie_by_id/${props.match.params.id}`, result => {
                let appListArr = [];
    
                result[0].apps.forEach((data, i) => {
                    appListArr.push(APP_SUPPORTED[findWithAttr(APP_SUPPORTED, 'value', data.app)].name);
                })
    
                setMovieId(result[0]._id);
                setAppList(appListArr.join(", "));
                setData(result[0]);
            })
        }else{
            const redirectData = JSON.parse(localStorage.getItem('detailRedirectData'));
            API.post('/get_movie', redirectData, result => {
                setMovieId(result.data._id)
                setData(result.data);
            })
        }

        API.get('/get_random_new_movies', res => {
            setRecommendMovies(res.data);
        })
    }

    if(!data || !recommendMovies || !movieId) return <LoadingScreen/>

    return(
        <div>
            <div className="backdrop" style={{backgroundImage: `url(${data.poster})`}}></div>
            <div className="info_container">
                <div className="col-left">
                    <div className="poster" style={{backgroundImage: `url(${data.poster})`}}></div>
                    <ActionContainer movieId={movieId}/>
                    <InformationGroup data={data}/>
                </div>
                <div className="col-right">
                    <div className="title_block">
                        <h1 className="title">{data.title} <span className="year">({data.release_year})</span></h1>
                        <div className="share_group">
                            <a href={`https://www.facebook.com/sharer/sharer.php?u=${window.location.origin}/movie/${data._id}`} target="_blank"><FaFacebook className="btn"/></a>
                            <a href={`https://twitter.com/intent/tweet?url=${window.location.origin}/movie/${data._id}&utm_medium=web&text=Family%20Guy%20-%20watch%20tv%20show%20streaming%20online`} target="_blank"><FaTwitter className="btn"/></a>
                            <FaRegCopy className="btn"/>
                        </div>
                    </div>
                    <AppsSupportedGrid apps={data.apps}/>

                    {data.trailer ? 
                        <div>
                            <h4 className="title">VIDEOS: TRAILERS, TEASERS, FEATURETTES</h4>
                            <iframe className="trailer_iframe" src={`${data.trailer}`} frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                        </div> : null}
                
                    {data.cast ? 
                        <div>
                            <h4 className="title">CAST</h4>
                            <CastList cast={data.cast}/>
                        </div> : null}

                    <h4 className="title">SYNOPSIS</h4>
                    <p className="text">{data.overview}</p>
                
                    <h4 className="title">{data.title.toUpperCase()} - WATCH ONLINE: WEBSITE, FREE OR RENT</h4>
                    <p className="text">Currently you are able to watch "{data.title}" streaming on {appList}.</p>

                    <h4 className="title" style={{marginBottom: 10}}>{recommendMovies.title.toUpperCase()}</h4>
                    <DiscoveryList data={recommendMovies.movies}/>
                </div>
            </div>
        </div>
    )
}

export default Movie;